.rmdbMovieThumb{
  max-width: 100%;
  overflow: hidden;
  position: relative;
  border-radius: 2%;
}

.image-container{
  overflow: hidden;
}
.rmdbMovieThumb img {
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width:100%;
  height: 100%;
}

.rmdbMovieThumb:hover img{
  -webkit-transform: scale(1.05);
      -ms-transform: scale(1.05);
          transform: scale(1.05);
}
.no_hover img:hover{
  -webkit-transform: scale(1) !important;
      -ms-transform: scale(1) !important;
          transform: scale(1) !important;
}

.movieThumbTitle{
  position: absolute;
  bottom: 2.5%;
  color: #fff; 
  font-size: 14px;
  padding: 0 14px;
}
.movieThumbTitle:hover{
  color:#919191;
  -webkit-transition: all 0.2s;
  -o-transition: all 0.2s;
  transition: all 0.2s;
}

@media screen and (max-width: 995px) {

  .movieThumbTitle{
    color: #fff; 
    font-size: 15px;
  }
}

@media screen and (max-width: 500px){

  .movieThumbTitle{
    color: #fff; 
    font-size: 13px !important;
  }
}


